import { types } from './actions';

const INITIAL_STATE = {
  list: [],
  scheduled: [],
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case types.GET_LIST: {
      return { ...state, list: action.payload.data };
    }
    case types.GET_SCHEDULED_LIST: {
      return { ...state, scheduled: action.payload.data };
    }
    default: {
      return state;
    }
  }
};



